"use client";
import { useEffect, useRef, useState } from "react";

const TILE_SIZE = 32;
const MAP_WIDTH = 40;
const MAP_HEIGHT = 30;

const mapObjects = [
  { id: "portal", type: "portal", x: 20, y: 2, width: 2, height: 2, sprite: "/sprites/environment/magic-portal.png" },
  { id: "fountain", type: "decor", x: 18, y: 15, width: 3, height: 3, sprite: "/sprites/environment/magic-fountain.png" },
  { id: "market_stall1", type: "shop", x: 5, y: 10, width: 2, height: 2, sprite: "/sprites/environment/market-stall.png" },
  { id: "market_stall2", type: "shop", x: 7, y: 12, width: 2, height: 2, sprite: "/sprites/environment/market-stall2.png" },
  { id: "blacksmith", type: "building", x: 25, y: 5, width: 3, height: 3, sprite: "/sprites/buildings/blacksmith.png" },
  { id: "inn", type: "building", x: 30, y: 8, width: 4, height: 4, sprite: "/sprites/buildings/inn.png" },
  { id: "npc1", type: "npc", x: 5, y: 5, width: 1, height: 2, sprite: "/sprites/characters/guild-master.png" },
  { id: "npc2", type: "npc", x: 10, y: 15, width: 1, height: 2, sprite: "/sprites/characters/village-merchant.png" },
  { id: "npc3", type: "npc", x: 27, y: 6, width: 1, height: 2, sprite: "/sprites/characters/blacksmith.png" },
  { id: "statue", type: "decor", x: 15, y: 10, width: 2, height: 3, sprite: "/sprites/environment/hero-statue.png" },
  { id: "chest", type: "treasure", x: 12, y: 14, width: 1, height: 1, sprite: "/sprites/environment/mystic-chest.png" },
];

export default function PixelVerseMap() {
  const canvasRef = useRef(null);
  const [player, setPlayer] = useState({ x: 15, y: 20 });

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");
    canvas.width = MAP_WIDTH * TILE_SIZE;
    canvas.height = MAP_HEIGHT * TILE_SIZE;

    const drawMap = () => {
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      ctx.fillStyle = "#8B5A2B";
      ctx.fillRect(0, 0, canvas.width, canvas.height);

      mapObjects.forEach((obj) => {
        const img = new Image();
        img.src = obj.sprite;
        img.onload = () => {
          ctx.drawImage(img, obj.x * TILE_SIZE, obj.y * TILE_SIZE, obj.width * TILE_SIZE, obj.height * TILE_SIZE);
        };
      });

      ctx.fillStyle = "gold";
      ctx.fillRect(player.x * TILE_SIZE, player.y * TILE_SIZE, TILE_SIZE, TILE_SIZE);
    };

    drawMap();
  }, [player]);

  const handleKeyDown = (event) => {
    setPlayer((prev) => {
      let newPos = { ...prev };
      if (event.key === "ArrowUp") newPos.y -= 1;
      if (event.key === "ArrowDown") newPos.y += 1;
      if (event.key === "ArrowLeft") newPos.x -= 1;
      if (event.key === "ArrowRight") newPos.x += 1;
      return newPos;
    });
  };

  useEffect(() => {
    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, []);

  return <canvas ref={canvasRef} className="border border-gray-700" />;
}
